import React, { Component } from "react";
import ReactGA from "react-ga";
import $ from "jquery";
import "./App.css";
import Header from "./Components/Header";
import Footer from "./Components/Footer";
// import About from "./Components/About";
// import Resume from "./Components/Resume";
import Contact from "./Components/Contact";
// import Portfolio from "./Components/Portfolio";
import OurExpertise from "./Components/ourexpertise";
import Consulting from "./Components/consultingservice";
import Service from "./Components/Service";
import Specification from "./Components/spicification";
// import Infibanner from "./Components/infinitebanner";
import Benefits from "./Components/certificationbenefits";
import OurTeam from "./Components/ourteam";
import Testimonials from "./Components/testimonials";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      foo: "bar",
      resumeData: {},
    };

    ReactGA.initialize("UA-110570651-1");
    ReactGA.pageview(window.location.pathname);
  }

  getResumeData() {
    $.ajax({
      url: "./resumeData.json",
      dataType: "json",
      cache: false,
      success: function (data) {
        this.setState({ resumeData: data });
      }.bind(this),
      error: function (xhr, status, err) {
        console.log(err);
        alert(err);
      },
    });
  }

  componentDidMount() {
    this.getResumeData();
  }

  render() {
    return (
      <div className="App">
        {/* <Infibanner data={this.state.resumeData.main} /> */}
        <Header data={this.state.resumeData.main} />
        <OurExpertise data={this.state.resumeData.main} />
        {/* <About data={this.state.resumeData.main} /> */}
        <Consulting data={this.state.resumeData.main} />
        <Service data={this.state.resumeData.main} />
        <Specification data={this.state.resumeData.main} />
        <Benefits data={this.state.resumeData.main} />
        {/* <OurTeam data={this.state.resumeData.main} /> */}
        <Testimonials data={this.state.resumeData.main} />
        {/* <Resume data={this.state.resumeData.resume} /> */}
        {/* <Portfolio data={this.state.resumeData.portfolio} /> */}
        <Contact data={this.state.resumeData.main} />
        <Footer data={this.state.resumeData.main} />
      </div>
    );
  }
}

export default App;
